export default {
	name: 'UniSwiperDot',
	props: {
		info: {
			type: Array,
			default () {
				return []
			}
		},
		current: {
			type: Number,
			default: 0
		},
		dotsStyles: {
			type: Object,
			default () {
				return {}
			}
		},
		// 类型 ：default(默认) indexes long nav
		mode: {
			type: String,
			default: 'long'
		},
	},
	data() {
		return {
			dots: {
				width: 6,
				height: 6,
				color: '#fff',
				backgroundColor: 'rgba(255, 255, 255, 0.4)',
				selectedBackgroundColor: '#fff'
			}
		}
	},
	watch: {
		dotsStyles(newVal) {
			this.dots = Object.assign(this.dots, this.dotsStyles)
		},
		mode(newVal) {
			if (newVal === 'indexes') {
				this.dots.width = 30
				this.dots.height = 6
			} else {
				this.dots.width = 6
				this.dots.height = 6
			}
		}

	},
	created() {
		if (this.mode === 'indexes') {
			this.dots.width = 30
			this.dots.height = 6
		}
		this.dots = Object.assign(this.dots, this.dotsStyles)
	}
}
